import websocket from './websocket';

const clientApi = {
  login({ token, login, password }) {
    const ws = websocket.instance;

    const message = JSON.stringify({
      type: 'auth',
      client: 'captain',
      token,
      login,
      password,
    });
    ws.send(message);

    return new Promise((resolve, reject) => {
      ws.onmessage = ({ data }) => {
        const parsedData = JSON.parse(data);
        if (parsedData.data.status === 'success') {
          resolve(parsedData.data);
        } else {
          reject(parsedData.data);
        }
      };
    });
  },
  setTeamName(token, deviceToken, name) {
    const ws = websocket.instance;

    const message = JSON.stringify({
      type: 'set_name',
      token,
      device_token: deviceToken,
      team_id: 1,
      name,
    });
    ws.send(message);

    return new Promise((resolve, reject) => {
      ws.onmessage = ({ data }) => {
        const parsedData = JSON.parse(data);
        if (parsedData.data) {
          resolve(parsedData.data);
        } else {
          reject(parsedData.data);
        }
      };
    });
  },
  sendAnswer(token, deviceToken, answer) {
    const ws = websocket.instance;

    const message = JSON.stringify({
      type: 'set_answer',
      token,
      device_token: deviceToken,
      team_id: 1,
      answer,
    });
    ws.send(message);

    return new Promise((resolve, reject) => {
      ws.onmessage = ({ data }) => {
        const parsedData = JSON.parse(data);
        if (parsedData.data) {
          resolve(parsedData.data);
        } else {
          reject(parsedData.data);
        }
      };
    });
  },
};

export default clientApi;
