<template>
  <v-frame id="question">
    <div class="question_content">
      <p data-role="question_container">
        <img data-role="question_image" src="" alt="" data-action="enlarge_image">

        <video data-role="question_video" data-action="enlarge_video" playsinline>
          <source type="video/mp4" src="">
        </video>

        <audio src="" data-role="question_audio" />

        <span data-role="question_text">
          Произошла ошибка. Проверьте, не открыто ли
          несколько вкладок с 1 ip-адреса и обновите страницу.
        </span>
      </p>
    </div>

    <div class="answers_block" data-role="answers_block">
      <div class="expand_button" data-action="expand_answers_options">
        <figure>
          <expand-icon />
        </figure>

        <div class="mobile_text">
          <p data-role="expand_answers_options_title">
            {{ $t("show_multiple_answer_options") }}
          </p>
        </div>
      </div>

      <div class="answers_options" data-role="answers_options">
        <div class="answer_option template" data-role="template">
          <p></p>
        </div>
      </div>
    </div>

    <div class="timer">
      <div class="progress" data-role="timer_progress"></div>
    </div>

    <template #controls>
      <div class="controls_block" data-role="question_controls_block">
        <div
          v-if="type === 'video'"
          class="media_buttons"
          data-role="video_media_buttons"
        >
          <div class="type">
            <media-video-icon />
          </div>

          <button data-role="pause_video_button" data-action="pause_video">
            <media-pause-icon />
          </button>

          <button data-role="play_video_button" data-action="play_video">
            <media-play-icon />
          </button>

          <button data-role="restart_video_button" data-action="restart_video">
            <media-stop-icon />
          </button>
        </div>

        <div
          v-if="type === 'audio'"
          class="media_buttons"
          data-role="audio_media_buttons"
        >
          <div class="type">
            <media-audio-icon />
          </div>

          <button data-role="pause_audio_button" data-action="pause_audio">
            <media-pause-icon />
          </button>

          <button data-role="play_audio_button" data-action="play_audio">
            <media-play-icon />
          </button>

          <button class="active" data-role="restart_audio_button" data-action="restart_audio">
            <media-stop-icon />
          </button>
        </div>

        <div
          v-if="type === 'text'"
          class="timer_buttons"
        >
          <div class="type">
            <timer-icon />
          </div>

          <button data-action="pause_timer" @click="pauseTimer">
            <media-pause-icon />
          </button>

          <button data-action="play_timer" @click="startTimer">
            <media-play-icon />
          </button>
        </div>
      </div>
    </template>
  </v-frame>
</template>

<script>
import VFrame from '@/components/ui/VFrame.vue';

import MediaAudioIcon from '@/icons/MediaAudio.vue';
import ExpandIcon from '@/icons/Expand.vue';
import MediaVideoIcon from '@/icons/MediaVideo.vue';
import MediaPauseIcon from '@/icons/MediaPause.vue';
import MediaPlayIcon from '@/icons/MediaPlay.vue';
import MediaStopIcon from '@/icons/MediaStop.vue';
import TimerIcon from '@/icons/Timer.vue';
import { useStore } from 'vuex';
import mainApi from '@/api';

export default {
  name: 'Question',
  components: {
    TimerIcon,
    MediaStopIcon,
    MediaPlayIcon,
    MediaPauseIcon,
    MediaVideoIcon,
    ExpandIcon,
    MediaAudioIcon,
    VFrame,
  },
  props: {
    type: {
      type: String,
      default: 'text',
      validator: value => ['text', 'video', 'audio'].includes(value),
    },
    token: {
      type: String,
      default: '',
    },
  },
  setup(props) {
    const store = useStore();
    const deviceToken = localStorage.getItem('deviceToken');
    const startTimer = () => {
      mainApi.stateChanger('play_timer', props.token, deviceToken).then(response => {
        store.commit('setState', response);
      });
    };

    const pauseTimer = () => {
      mainApi.stateChanger('pause_timer', props.token, deviceToken).then(response => {
        store.commit('setState', response);
      });
    };
    return {
      startTimer,
      pauseTimer,
    };
  },
};
</script>

<style lang="less">
.timer_buttons {
  svg path,
  svg rect {
    fill: #E37F3B
  }
}
</style>
