<template>
  <section id="auth" data-role="auth_form" class="active">
    <form class="form" @submit.prevent="handleSubmit">
      <h2>{{ $t("quantum_master") }}</h2>

      <label>
        <input
          v-model="password"
          type="password"
          data-role="pass"
          class="form_element"
          :placeholder="$t('password')"
        />
      </label>

      <button
        class="auth"
        data-action="auth"
      >
        <span> {{ $t("sign_in") }} </span>
      </button>
    </form>
  </section>
</template>

<script>
import { ref } from 'vue';

export default {
  name: 'Login',
  emits: ['submit'],
  setup(_, { emit }) {
    const password = ref('');

    const handleSubmit = () => {
      emit('submit', password.value);
    };

    return {
      password,
      handleSubmit,
    };
  },
};
</script>
