<template>
  <background />
  <wrapper>
    <game-header
      :show-menu-button="isLoggedIn"
      @menu-button-click="isMenuShown = true"
      @chat-click="isChatShown = true"
    />

    <component
      v-if="isLoggedIn"
      :token="token"
      :is="componentKeys[currentState]"
    />

    <login
      v-else
      @submit="onLogin"
    />
  </wrapper>

  <v-menu
    v-model:active="isMenuShown"
    @menu-click="handleMenuClick"
  />

  <popup-tabs v-model:active="isChatShown"/>
</template>

<script>
import '@/assets/css/style_master.less';

import {
  computed,
  onBeforeMount,
  ref,
} from 'vue';

import { useStore } from 'vuex';
import masterApi from '@/api/master';
import websocket from '@/api/websocket';

import useConfig from '@/modules/core/hooks/useConfig';

import URLS from '@/data/urls';

import Wrapper from '@/components/Wrapper.vue';
import Background from '@/components/Background.vue';
import GameHeader from '@/components/GameHeader.vue';
import TeamAnswers from '@/components/TeamAnswers.vue';
import PopupTabs from '@/components/popups/PopupTabs.vue';

import mainApi from '@/api';
import componentKeys from '../data/componentKeys';

import Login from '../components/Login.vue';
import Start from '../components/Start.vue';
import Rules from '../components/Rules.vue';
import Question from '../components/Question.vue';
import ShowAnswer from '../components/ShowAnswer.vue';
import QuestionInfo from '../components/QuestionInfo.vue';
import Stats from '../components/Stats.vue';
import Welcome from '../components/Welcome.vue';
import VMenu from '../components/VMenu.vue';
import EditAnswers from '../components/EditAnswers.vue';
import QuestionList from '../components/QuestionList.vue';

export default {
  name: 'Master',
  components: {
    PopupTabs,
    VMenu,
    Welcome,
    Login,
    Wrapper,
    GameHeader,
    Background,
    Start,
    Rules,
    Question,
    ShowAnswer,
    QuestionInfo,
    Stats,
    TeamAnswers,
    QuestionList,
    EditAnswers,
  },
  props: {
    token: {
      type: String,
      default: '',
    },
  },
  setup(props) {
    const { setConfigState } = useConfig();

    const store = useStore();

    const isMenuShown = ref(false);
    const isChatShown = ref(false);

    const isLoggedIn = computed(() => store.state.isLoggedIn);
    const currentState = computed(() => store.state.state);

    websocket.init(`${URLS.WS_URL}?token=${props.token}`);

    onBeforeMount(async () => {
      document.title = 'Master';
    });

    const onLogin = password => {
      masterApi.login({ token: props.token, password }).then(res => {
        store.commit('setLoggedIn', true);
        localStorage.setItem('deviceToken', res.device_token);
        mainApi.stateChanger('get_state', props.token, res.device_token).then(response => {
          store.commit('setState', response);
        }).catch(e => {
          console.log(`${e.status} detail:${e.detail}`);
        });
        // setConfigState('welcome');
      }).catch(e => {
        console.log(`${e.status} detail:${e.detail}`);
      });
    };

    const handleMenuClick = state => {
      setConfigState(state);

      isMenuShown.value = false;
    };

    return {
      componentKeys,
      isMenuShown,
      isLoggedIn,
      isChatShown,
      currentState,
      onLogin,
      setConfigState,
      handleMenuClick,
    };
  },
};
</script>
