<script>
import VFrame from '@/components/ui/VFrame.vue';

import ExpandIcon from '@/icons/Expand.vue';
import MediaAudioIcon from '@/icons/MediaAudio.vue';
import SendIcon from '@/icons/Send.vue';
import AnswerStatusIcon from '@/icons/AnswerStatus.vue';
import { ref } from 'vue';
import clientApi from '@/api/client';

export default {
  name: 'Question',
  components: {
    AnswerStatusIcon,
    ExpandIcon,
    MediaAudioIcon,
    SendIcon,
    VFrame,
  },
  props: {
    token: {
      type: String,
      default: '',
    },
  },
  setup(props) {
    const deviceToken = localStorage.getItem('deviceToken');
    const answer = ref('');
    const sendAnswer = () => {
      clientApi.sendAnswer(props.token, deviceToken, answer).then(response => {
        console.log(response);
      });
    };
    return { sendAnswer, answer };
  },
};
</script>

