import errorLogger from '@/utils/errorLogger';

import { post } from './index';
import websocket from './websocket';

import URLS from '../data/urls';

const masterApi = {
  async checkToken(token) {
    try {
      return await post({
        url: URLS.CHECK_TOKEN,
        data: { token },
      });
    } catch (errors) {
      errorLogger(errors);

      throw errors;
    }
  },
  login({ token, password }) {
    const ws = websocket.instance;
    const message = JSON.stringify({
      type: 'auth',
      client: 'master',
      token,
      password,
    });

    ws.send(message);

    return new Promise((resolve, reject) => {
      ws.onmessage = ({ data }) => {
        const parsedData = JSON.parse(data);
        if (parsedData.data.status === 'success') {
          resolve(parsedData.data);
        } else {
          reject(parsedData.data);
        }
      };
    });
  },
  setRules(token, deviceToken) {
    const ws = websocket.instance;
    const message = JSON.stringify({
      type: 'rules',
      token,
      device_token: deviceToken,
    });

    ws.send(message);

    return new Promise((resolve, reject) => {
      ws.onmessage = ({ data }) => {
        const parsedData = JSON.parse(data);
        if (parsedData.data.state === 'rules') {
          resolve(parsedData.data);
        } else {
          reject(parsedData.data);
        }
      };
    });
  },
  async fetchConfig() {
    const { default: data } = await import('@/data/config.json');

    return data;
  },
  async fetchQuestions() {
    const { default: data } = await import('@/data/question_master.json');

    return data;
  },
};

export default masterApi;
