<template>
  <v-frame id="rules">
    <div class="title">
      <div class="left" />

      <p>{{ $t("rules") }}:</p>

      <div class="right" />
    </div>

    <div class="rules_content" data-role="rules_content">
      {{ rules }}
    </div>

    <button
      class="button circle"
      data-action="next_question"
      @click="handleClick"
    >
      <question-icon />
    </button>
  </v-frame>
</template>

<script>
import VFrame from '@/components/ui/VFrame.vue';

import QuestionIcon from '@/icons/Question.vue';
import mainApi from '@/api';
import { useStore } from 'vuex';

export default {
  name: 'Rules',
  components: { VFrame, QuestionIcon },
  props: {
    rules: {
      type: String,
      required: true,
    },
    token: {
      type: String,
      default: '',
    },
  },
  setup(props) {
    const store = useStore();
    const deviceToken = localStorage.getItem('deviceToken');

    const handleClick = () => {
      mainApi.stateChanger('next_question', props.token, deviceToken).then(response => {
        store.commit('setConfig', response);
      });
      store.commit('setState', 'question_info');
      // for test
    };

    return {
      handleClick,
    };
  },
};
</script>
