<template>
  <v-frame id="question_info">
    <template v-if="type === 'bets'">
      <div class="sub_title">
        <p>{{ $t("question_price") }}</p>
      </div>

      <div class="cost">
        <div class="glowing_container question">
          <p data-role="question_cost">0</p>
        </div>
      </div>
    </template>

    <div class="sub_title total_cost_title">
      <p>{{ $t("question_pot") }}</p>
    </div>

    <div class="cost total_cost">
      <div class="glowing_container pot">
        <p data-role=total_pot>0</p>
      </div>
    </div>

    <button
      class="button circle"
      data-action="show_question"
      @click="handleClick"
    >
      <question-icon />
    </button>
  </v-frame>
</template>

<script>

import QuestionIcon from '@/icons/Question.vue';
import mainApi from '@/api';
import { useStore } from 'vuex';
import VFrame from '../../../components/ui/VFrame.vue';

export default {
  name: 'QuestionInfo',
  components: { VFrame, QuestionIcon },
  props: {
    type: {
      type: String,
      required: true,
    },
    token: {
      type: String,
      default: '',
    },
  },
  setup(props) {
    const store = useStore();
    const deviceToken = localStorage.getItem('deviceToken');
    const handleClick = () => {
      mainApi.stateChanger('show_question', props.token, deviceToken).then(response => {
        store.commit('setState', response);
      });
      store.commit('setState', 'question');
    };

    return {
      handleClick,
    };
  },
};
</script>
