<template>
  <section id="auth" data-role="auth_form" class="active">
    <form class="form" @submit.prevent="handleSubmit">
      <h2>{{ $t("quantum_player") }}</h2>
      <label>
        <input
          v-model="loginCredentials.login"
          type="text"
          data-role="login"
          class="form_element"
          :placeholder="$t('login')"
        />
      </label>
      <label>
        <input
          v-model="loginCredentials.password"
          type="password"
          data-role="pass"
          class="form_element"
          :placeholder="$t('password')"
        />
      </label>
      <button
        class="auth"
        data-action="auth"
      >
        <span> {{ $t("sign_in") }} </span>
      </button>
    </form>
  </section>
</template>

<script>
import { reactive } from 'vue';

export default {
  name: 'Login',
  emits: ['submit'],
  setup(_, { emit }) {
    const loginCredentials = reactive({
      login: '',
      password: '',
    });
    const handleSubmit = () => {
      emit('submit', loginCredentials.login, loginCredentials.password);
    };

    return {
      handleSubmit,
      loginCredentials,
    };
  },
};
</script>
