<template>
  <router-view :token="token" />
</template>

<script>
import { useRouter } from 'vue-router';
import { computed } from 'vue';

import api from '@/api/master';

import URLS from '@/data/urls';

export default {
  name: 'App',
  setup() {
    const router = useRouter();

    const token = computed(() => (
      router.currentRoute.value.fullPath
        .replace('/', '')
        .replace('m=', '')
        .replace('c=', '')
    ));

    router.isReady().then(async () => {
      try {
        const data = await api.checkToken(token.value);

        if (data.status === 'error') {
          if (data.detail === 'game finished') {
            await router.push(`${URLS.BASE_URL}/fallback.php?finished`);
          } else if (data.detail === 'wrong token' || data.detail === 'empty token') {
            await router.push(`${URLS.BASE_URL}/fallback.php?corrupted`);
          }
        }
      } catch {
        await router.push(`${URLS.BASE_URL}/fallback.php`);
      }
    });

    return {
      token,
    };
  },
};
</script>

<style>
#app {
  height: 100%;
}
</style>
