<template>
  <div
    id="popup_confirm"
    class="popup_dialog"
    :class="{ 'active': active }"
    data-role="popup_confirm"
  >
    <div class="container">
      <div class="inner_container">
        <div class="top" />

        <div class="title">
          <div class="left" />

          <p>{{ title }}</p>

          <div class="right" />
        </div>

        <div class="controls_block">
          <div
            class="button rectangle"
            data-action="confirm"
            @click="$emit('confirm')"
          >
            {{ $t("yes") }}
          </div>

          <div
            class="button rectangle"
            data-action="close_popup"
            @click="$emit('close')"
          >
            {{ $t("no") }}
          </div>
        </div>

        <div class="bottom" />
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'PopupConfirm',
  props: {
    active: {
      type: Boolean,
      default: false,
    },
    title: {
      type: String,
      required: true,
    },
  },
  emits: ['close', 'confirm'],
};
</script>
