<template>
  <v-frame id="team_name">
    <div class="title">
      <div class="left" />

      <p>{{ $t("enter_team_name") }}</p>

      <div class="right" />
    </div>

    <div class="team_name">
      <label class="input">
        <input
          type="text"
          placeholder=""
          maxlength="35"
          data-role="team_name_input"
          v-model="teamName"
        />
      </label>

      <button data-action="set_team_name" @click="handleClick">
        {{ $t("ready") }}
      </button>
    </div>
  </v-frame>

</template>

<script>
import VFrame from '@/components/ui/VFrame.vue';
import { ref } from 'vue';
import cientApi from '@/api/client';

export default {
  name: 'TeamName',
  components: { VFrame },
  props: {
    token: {
      type: String,
      default: '',
    },
  },
  setup(props) {
    const teamName = ref('');
    const deviceToken = localStorage.getItem('deviceToken');
    const handleClick = () => {
      cientApi.setTeamName(props.token, deviceToken, teamName.value).then(response => {
        console.log(response);
      });
    };
    return {
      handleClick,
      teamName,
    };
  },
};
</script>
