<template>
  <section id="welcome" data-role="welcome">
    <div class="center">
      <button
        class="button circle"
        data-action="start"
        @click="handleClick"
      >
        <welcome-start-icon />
      </button>
    </div>
  </section>
</template>

<script>

import WelcomeStartIcon from '@/icons/WelcomeStart.vue';
import mainApi from '@/api';
import { useStore } from 'vuex';

export default {
  name: 'Welcome',
  components: { WelcomeStartIcon },
  props: {
    token: {
      type: String,
      default: '',
    },
  },
  setup(props) {
    const store = useStore();
    const deviceToken = localStorage.getItem('deviceToken');

    const handleClick = () => {
      mainApi.stateChanger('start', props.token, deviceToken).then(response => {
        store.commit('setState', response);
      });
    };

    return {
      handleClick,
    };
  },
};
</script>
