<template>
  <div
    id="popup_tabs"
    ref="popupTabs"
    class="master"
    :class="{
      'active': active,
      'maximized': isMaximized,
    }"
    data-role="popup_tabs"
  >
    <div class="popup_controls">
      <button
        ref="dragButton"
        class="drag_popup"
        data-action="drag_popup"
      >
        <move-icon />
      </button>

      <button
        class="toggle_stream_size"
        data-action="toggle_popup_tabs_size"
        @click="isMaximized = !isMaximized"
      >
       <toggle-size-icon />
      </button>

      <button
        class="close_stream"
        data-action="close_popup_tabs"
        @click="$emit('update:active', false)"
      >
        <close-icon />
      </button>
    </div>

    <div class="tab_names" data-action="change_tab">
      <button
        v-if="!isLiteSubscription"
        :class="{ active: currentTab === 'stream' }"
        @click="currentTab = 'stream'"
      >
        Стрим
      </button>

      <button
        :class="{ active: currentTab === 'chat' }"
        @click="currentTab = 'chat'"
      >
        Чат
      </button>
    </div>

    <div class="tabs" data-role="tabs">
      <div
        v-if="!isLiteSubscription"
        id="popup_stream"
        data-role="popup_stream"
        class="start_stream"
        :class="{ active: currentTab === 'stream' }"
      >
        <div class="stream_state_controls">
          <div class="loader">
            <div class="sector" />
            <div class="sector" />
            <div class="sector" />
            <div class="sector" />
          </div>

          <div class="controls_container">
            <button data-action="start_stream" data-type="video_hd">
              Видео (качество)
            </button>

            <button data-action="start_stream" data-type="video">
              Видео (стабильность)
            </button>

            <button data-action="start_stream" data-type="audio">
              Аудио
            </button>
          </div>
        </div>

        <div id="stream_video" data-role="stream_video" />

        <div class="stream_controls">
          <div class="disable_video_stream" data-action="disable_video_stream">
            <disable-video-icon />
          </div>

          <div data-action="turn_off_stream">
            <turn-off-icon />
          </div>
        </div>
      </div>

      <div
        id="popup_chat"
        data-role="popup_chat"
        :class="{ active: currentTab === 'chat' }"
      >
        <div class="messages" data-role="messages"/>

        <div class="chat_controls">
          <div class="input">
            <label>
              <input type="text" data-role="chat_message" maxlength="500">
            </label>

            <button data-action="send_chat_message">
              <send-icon />
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { ref } from 'vue';

import MoveIcon from '@/icons/Move.vue';
import ToggleSizeIcon from '@/icons/ToggleSize.vue';
import DisableVideoIcon from '@/icons/DisableVideo.vue';
import TurnOffIcon from '@/icons/TurnOff.vue';
import SendIcon from '@/icons/Send.vue';
import CloseIcon from '@/icons/Close.vue';

export default {
  name: 'PopupTabs',
  components: {
    CloseIcon,
    MoveIcon,
    ToggleSizeIcon,
    DisableVideoIcon,
    TurnOffIcon,
    SendIcon,
  },
  props: {
    isLiteSubscription: {
      type: Boolean,
      default: false,
    },
    active: {
      type: Boolean,
      default: false,
    },
  },
  emits: ['update:active'],
  setup() {
    const isMaximized = ref(false);
    const currentTab = ref('chat');

    return {
      isMaximized,
      currentTab,
    };
  },
};
</script>
