<template>
  <v-frame id="start">
    <div class="title">
      <div class="left" />

      <p>{{ $t("authenticated_teams") }}</p>

      <div class="right"/>
    </div>

    <div class="glowing_container" data-role="authorized_teams">
      <div class="template" data-role="template">
        <div class="id table_cell" data-role="id" />

        <div class="name table_cell" data-role="name" />

        <div class="kick table_cell" data-action="kick">
          <img src="../../../assets/img/Delete_50px.png" alt="">
        </div>
      </div>

      <div class="id table_head">ID</div>

      <div class="name table_head">{{ $t("team_name") }}</div>
    </div>

    <button
      class="button circle"
      data-action="show_rules"
      @click="isConfirmShown = true"
    >
      <start-rules-icon />
    </button>
  </v-frame>

  <popup-confirm
    :active="isConfirmShown"
    :title="$t('show_rules_confirm')"
    @confirm="handleConfirm"
    @close="isConfirmShown = false"
  />
</template>

<script>
import { ref } from 'vue';

import VFrame from '@/components/ui/VFrame.vue';

import StartRulesIcon from '@/icons/StartRules.vue';

import mainApi from '@/api';
import { useStore } from 'vuex';
import PopupConfirm from './popups/PopupConfirm.vue';

export default {
  name: 'Start',
  components: {
    PopupConfirm,
    VFrame,
    StartRulesIcon,
  },
  props: {
    token: {
      type: String,
      default: '',
    },
  },
  setup(props) {
    const isConfirmShown = ref(false);
    const store = useStore();
    const deviceToken = localStorage.getItem('deviceToken');

    const handleConfirm = () => {
      mainApi.stateChanger('rules', props.token, deviceToken).then(response => {
        store.commit('setState', response);
      });
      isConfirmShown.value = false;
    };

    return {
      isConfirmShown,
      handleConfirm,
    };
  },
};
</script>
