import { createRouter, createWebHistory } from 'vue-router';

import Master from '@/modules/master/views/Master.vue';
import Client from '@/modules/client/views/Client.vue';
import NotFound from '@/modules/core/views/NotFound.vue';

const routes = [
  {
    path: '/m=:pathMatch(.*)*',
    name: 'Master',
    component: Master,
  },
  {
    path: '/c=:pathMatch(.*)*',
    name: 'Client',
    component: Client,
  },
  {
    path: '/:pathMatch(.*)*',
    name: 'NotFound',
    component: NotFound,
  },
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

export default router;
